import {createAsyncThunk} from '@reduxjs/toolkit';

export const serializeError = error => {
    const {
        isAxiosError,
        response,
        name,
        type,
        message,
        code,
        errorCode,
        stack,
    } = error;
    const newVar = {
        name,
        type,
        message,
        code,
        stack,
        errorCode: (isAxiosError && response)
            ? response.status
            : (errorCode || code),
    };
    return filterEntries((key, value) => value !== undefined)(newVar);
};
export const filterEntries = filter => target => Object
    .entries(target)
    .filter(([key, value], index, entries) => filter(key, value, index, entries))
    .reduce((result, [key, value]) => ({
        ...result,
        [key]: value,
    }), {});

export const axiosThunk = thunk => async (arg, thunkAPI) => {
    try {
        const response = await thunk(arg, thunkAPI);
        return response.data;
    } catch (error) {
        if (error && error.response && error.response.data) {
            return thunkAPI.rejectWithValue(error.response.data);
        } else {
            return thunkAPI.rejectWithValue(serializeError(error));
        }
    }
};

export const createAxiosThunk = (typePrefix, payloadCreator) => (
    createAsyncThunk(typePrefix, axiosThunk(payloadCreator))
);

export const noopThunk = createAsyncThunk('noop', () => null);
