import {createSlice} from '@reduxjs/toolkit';
import {getFortunes} from './fortunesApi';
import {createAxiosThunk} from './reduxUtil';

export const fetchFortunes = createAxiosThunk(
    'account/fetchFortunes',
    () => getFortunes()
);

const initialState = {
    isLoading: false,
    fortunes: null,
};
const fortunesSlice = createSlice({
    name: 'fortunes',
    initialState,
    extraReducers: {
        [fetchFortunes.fulfilled]: (state, {payload}) => {
            state.fortunes = payload;
        },
    },
});

export default fortunesSlice.reducer;
